@import url('https://fonts.googleapis.com/css?family=Open+Sans:600|Work+Sans:400,500,600,700&display=swap');

body {
	background: rgb(34, 193, 195);
	background: linear-gradient(
		120deg,
		rgba(34, 193, 195, 1) 0%,
		rgba(253, 187, 45, 1) 100%
	);
	font-family: 'work sans';
	height: 100%;
}

html {
	font-size: 100%;
	height: 100%;
}

#loginform {
	max-width: 600px !important;
	min-width: 450px !important;
	width: 30% !important;
	height: 60% !important;
	margin: 100px auto !important;
	background-color: #ffffff !important;
	border-radius: 25px !important;
	margin-top: 0 !important;
	box-shadow: 10px 10px 5px 12px grey;
}

#headerTitle {
	text-align: center;
	font-family: 'open sans', sans-serif;
	padding: 2rem 0;
	margin: 0;
	font-size: 2rem;
}

.rowReg {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-top: 2rem;
	max-width: 100%;
	margin: 0 !important;
}

.rowReg input {
	width: 80%;
	box-sizing: border-box;
	border: none;
	font-size: 1.3rem;
	padding-left: 1.5rem;
	padding-bottom: 1rem;
	box-shadow: inset 0px -3px 0px 0px rgba(187, 187, 187, 0.2);
	transition: box-shadow 0.2s ease-in;
}

.rowReg input:focus {
	box-shadow: inset 0px -3px 0px 0px rgba(34, 193, 195, 0.7);
	outline: none;
}

.rowReg input::-webkit-input-placeholder {
	opacity: 1;
	transition: opacity 0.25s ease-out;
}

.rowReg input:hover::-webkit-input-placeholder,
.rowReg input:focus::-webkit-input-placeholder {
	opacity: 0;
}

.rowReg label {
	align-self: start;
	padding-left: 4.5rem;
	padding-bottom: 0.5rem;
	color: rgba(187, 187, 187, 0.9);
}

.rowReg button {
	border-radius: 25px;
	width: 80%;
	height: 40px;
	font-size: 1.3rem;
	color: white;
	font-weight: 700;
	background: rgb(34, 193, 195);
	background: linear-gradient(
		90deg,
		rgba(34, 193, 195, 1) 0%,
		rgba(253, 187, 45, 1) 100%
	);
	border: 0px;
	cursor: pointer;
	transition: opacity 0.25s ease-out;
}
.rowReg a {
	border-radius: 25px;
	width: 80%;
	height: 40px;
	font-size: 1.3rem;
	color: white;
	font-weight: 700;
	background: rgb(34, 193, 195);
	background: linear-gradient(
		90deg,
		rgba(34, 193, 195, 1) 0%,
		rgba(253, 187, 45, 1) 100%
	);
	border: 0px;
	cursor: pointer;
	transition: opacity 0.25s ease-out;
	text-align: center;
}
.rowReg a:hover {
	opacity: 0.8;
	text-decoration: none;
	color: #333;
}

.rowReg button:hover {
	opacity: 0.8;
	color: #333;
}

#button {
	padding-bottom: 1.5rem;
}

#iconGroup {
	display: flex;
	justify-content: center;
	padding: 2rem 0;
}

#iconGroup a {
	margin: 0 0.6rem;
}
.icon {
	height: 50px;
	width: 50px;
	margin: 0 50px 0 50px;
}

#alternativeLogin {
	text-align: center;
	padding-top: 2rem;
	margin-top: 1.5rem;
}

.icon:hover {
	transition: 1s;

	transform: rotate(360deg);
}
#facebookIcon {
	border: none;
	background-color: white;
	cursor: pointer;
}
#googleIcon {
	border: none;
	background-color: white;
	cursor: pointer;
}
#containerNavbar {
	height: 125px;
}
.navbar-brand {
	height: 100%;
	width: 100%;
	margin-right: 1000px;
	padding: 0;
}
.navbar {
	padding: 10px;
	border-bottom: 10px solid #49b293;
	font-size: 20px;
}

#underNav {
	margin-left: auto;
	width: 100%;
	height: 100%;
	margin-right: auto;
	display: flex;
}
#formGeneral {
	align-self: flex-end;
	width: 50%;
	height: inherit;

	box-shadow: 10px 10px 5px 12px grey;
	background-color: white;
	margin: auto;
}
#header {
	height: 13%;
}
.row {
	width: 100%;
	display: flex;
	margin: 0;
	height: 100%;
}
.column {
	flex: 50%;
	background-color: white;
}
#tabG {
	height: 1000px !important;
}
.rGen {
	background-color: white;
	border: 0.5px solid grey;
	padding-bottom: 20px;
}
.rowReg select {
	width: 80%;
	box-sizing: border-box;
	border: none;
	font-size: 1.3rem;
	padding-left: 1.5rem;
	padding-bottom: 1rem;
	box-shadow: inset 0px -3px 0px 0px rgba(187, 187, 187, 0.2);
	transition: box-shadow 0.2s ease-in;
}

.rowReg select:focus {
	box-shadow: inset 0px -3px 0px 0px rgba(34, 193, 195, 0.7);
	outline: none;
}

.rowReg select::-webkit-select-placeholder {
	opacity: 1;
	transition: opacity 0.25s ease-out;
}

.rowReg select:hover::-webkit-select-placeholder,
.rowReg select:focus::-webkit-select-placeholder {
	opacity: 0;
}
.btnSend {
	margin-left: 80%;
	width: 15%;
	height: 50px;
	margin-right: 5% !important;
	border-radius: 5px;
	margin-top: 10px;
	margin-bottom: 10px;
	background-color: #4bb543;
}
.nav-item {
	margin-left: 10px;
	margin-top: 10px;
}

.rowJos {
	background-color: white !important;
}
.rowReg {
	border: none;
}

#root {
	width: 100%;
	height: 80% !important;
}

.navbar {
	height: 13%;
}
